* {
  box-sizing: border-box;
  margin: 0;
}

body {
  color: #222;
  background: #f9f6f1;
  margin: 0;
  padding: 16px;
  font-family: Arial, sans-serif;
}

.app {
  grid-template-columns: 1fr 320px;
  align-items: start;
  gap: 16px;
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
}

.app-header {
  grid-column: 1 / -1;
  margin-bottom: 16px;
}

.shopping {
  background: #fff;
  border-radius: 8px;
  padding: 12px;
  box-shadow: 0 2px 6px #00000080;
}

.card {
  background-color: #fff;
  border-radius: 12px;
  flex-direction: row;
  align-items: stretch;
  min-height: 220px;
  margin-bottom: 16px;
  transition: box-shadow .3s, transform .3s;
  display: flex;
  overflow: hidden;
  box-shadow: 0 2px 6px #00000080;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 16px #00000026;
}

.thumb {
  aspect-ratio: 4 / 3;
  flex: 50%;
  max-width: 50%;
  overflow: hidden;
}

.thumb img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.meta {
  flex-direction: column;
  flex: 50%;
  gap: 8px;
  max-width: 50%;
  padding: 16px;
  display: flex;
}

.card-buttons {
  justify-content: flex-end;
  gap: 8px;
  margin-top: auto;
  display: flex;
}

.card-buttons button {
  border-radius: 6px;
  padding: 4px 8px;
}

.toaster {
  z-index: 1000;
  width: 320px;
  position: fixed;
  bottom: 16px;
  right: calc(50% - 550px);
}

.toast {
  color: #fff;
  opacity: .95;
  background: #111;
  border-radius: 8px;
  margin-top: 8px;
  padding: 10px 12px;
}

#print-btn, #clear-btn {
  border: 1px solid #000;
  border-radius: 8px;
  padding: 10px 12px;
}

.small {
  color: #666;
  padding-top: 1rem;
  font-size: .85rem;
}

@media (width <= 1100px) {
  .toaster {
    right: 16px;
  }
}

@media (width <= 800px) {
  .app {
    grid-template-columns: 1fr;
  }

  .toaster {
    width: calc(100vw - 32px);
    max-width: 420px;
    bottom: 16px;
    right: 16px;
  }
}

@media print {
  .main-content, .toaster, .shopping-buttons, .note, .list-item button, .app-header {
    display: none;
  }

  .app {
    max-width: none;
    margin: 0;
    display: block;
  }

  .sidebar {
    width: 100%;
    position: static;
  }

  .shopping {
    box-shadow: none;
    background: #fff;
    border: none;
    width: 100%;
    padding: 0;
    position: static;
  }

  .list {
    border: none;
    max-height: none;
    overflow: visible;
  }
}
